import * as React from 'react'
import Layout from '../../components/layout'
import Seo from '../../components/seo'
import {
  aboutContainer,
  aboutFixed,
  aboutFlexItem,
  icon,
  meImg,
  aboutItem,
  dateTime,
  iconCalendar
} from '../../components/layout.module.css'
import me from '../../images/me.jpg'
import work from '../../images/suitcase.png'
import mail from '../../images/email.png'
import phone from '../../images/phone-call.png'
import map from '../../images/map.png'
import calendar from '../../images/calendar.png'

const AboutPage = () => {

  return (
    <Layout pageTitle="My projects">
      <div className={aboutContainer}>
        <div className={aboutFixed}>
          <img src={me} className={meImg} />
          <h2>Alex Bilesck</h2>
          <div className={aboutItem}>
            <img src={work} className={icon} />
            Game developer
          </div>
          <div className={aboutItem}>
            <img src={map} className={icon} />
            Sorocaba/SP, Brazil
          </div>
          <div className={aboutItem}>
            <img src={mail} className={icon} />
            alex.bilesck@gmail.com
          </div>
          <div className={aboutItem}>
            <img src={phone} className={icon} />
            +55 (15) 99839-4298
          </div>
          <hr />
          <h3>Skills</h3>
          <ul>
            <li>C programming</li>
            <li>C# programming</li>
            <li>Java programming</li>
            <li>Javascript programming</li>
            <li>SQL</li>
            <li>SQLite</li>
            <li>.Net framework</li>
            <li>SCRUM</li>
            <li>UML</li>
          </ul>
          <h3>Languages</h3>
          <ul>
            <li>English</li>
            <p>
              Advanced knowlegde
            </p>
            <li>Portuguese</li>
            <p>
              Native speaker
            </p>
            <li>Spanish</li>
            <p>
              Intermediate knowledge
            </p>
          </ul>
        </div>

        <div className={aboutFlexItem}>
          <div>
            <h2>Work experience</h2>
            <ul>
              <li>Unity Game Developer at LIGA</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                11/2021 - present days
              </p>
              <p>
                Work with models, assets and artwork implementation. C# scripts creation and multiple platforms builds (IOS, Android, Windows and WebGL).
              </p>
              <li>Industrial IT Intern at Prysmian Group</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                07/2021 - 10/2021
              </p>
              <p>
                Worked with production system implementation and maintance, besides PC maintance.
              </p>
              <li>Developer freelancer</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                01/2020 - present days
              </p>
              <p>
                Worked with static site development, Unity game development and apps development.
              </p>
            </ul>
          </div>
          <div className={aboutFlexItem}>
            <h2>Education</h2>
            <ul>
              <li>FACENS</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                2020-2024
              </p>
              <p>
                Computer engineer bachelor degree
              </p>
              <li>ETEC Fernando Prestes</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                2017-2019
              </p>
              <p>
                Computer technician course (system development focused)
              </p>
              <li>Microsoft Technology Associate - MTA</li>
              <p className={dateTime}>
                <img src={calendar} className={iconCalendar} />
                2019
              </p>
              <p>
                Microsoft Technology Associate - Software Development Fundamental
              </p>
            </ul>
          </div>
        </div>

      </div>
    </Layout>
  )
}

export const Head = () => <Seo title="About me" />

export default AboutPage